.item {
  width: 32.5%;
  @media screen and (max-width: 600px) {
    width: 100%;
  }

  .imgBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .wrapperImg {
      & img {
        width: 100%;
        height: 100%;
      }
    }
  }
}